* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.back-button {
  color: #000;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 4px;
  padding: 5px;
  text-decoration: none;
  display: inline-block;
}

.buttons-container-switcher {
  justify-content: center;
  align-items: center;
  padding: 400px;
  display: flex;
}

.button-switcher {
  width: 200px;
  height: 100px;
  cursor: pointer;
  border-width: 4px;
  border-color: #000;
  border-radius: 10px;
  margin-left: 8px;
  font-size: 30px;
}

button[disabled] {
  cursor: not-allowed;
  background-color: #afaeae;
}

.timer-container {
  flex-direction: column;
  padding-top: 200px;
  display: flex;
}

.selection-container {
  justify-content: center;
  display: flex;
}

#datetime-picker {
  width: 500px;
  height: 50px;
  text-align: center;
  border-radius: 6px;
  font-size: 24px;
}

.timer-button {
  height: 50px;
  width: 100px;
  border-radius: 6px;
  margin-left: 10px;
  display: inline-block;
}

.timer {
  text-transform: uppercase;
  justify-content: center;
  margin-top: 40px;
  margin-left: -45px;
  font-size: 60px;
  display: flex;
}

.field {
  max-width: 90px;
  flex-direction: column;
  justify-content: center;
  margin-left: 70px;
  display: flex;
}

.value {
  justify-content: center;
  display: flex;
}

.label {
  justify-content: center;
  align-items: center;
  font-size: 20px;
  display: flex;
}

